import { useState } from "react";
import Backdrop from '@mui/material/Backdrop';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';

import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import InfoIcon from '@mui/icons-material/Info';

import "./PackageDetails.scss";

const itemDetailValidations = {
    weight: 'Enter weight.',
    length: 'Enter length.',
    width: 'Enter width.',
    height: 'Enter height.',
    volume: 'Enter volume.',
    decValue: 'Enter declared value.',
    itemsCount: 'Select no of items.'
};

const PackageDetails = ({saveAndContinueItem, items}) => {

    const [itemDetails, setItemDetails] = useState({});
    const [itemList, setItemList] = useState(items);
    const [openAddItem, setOpenAddItem] = useState(false);
    const [editItemIndex, setEditItemIndex] = useState(null);

    const handleInputchange = (event) => {
        let { name, value } = event.target;
        setItemDetails({ ...itemDetails, [name]: value });
        console.log('Item', itemDetails);
    };

    const onAddNewItem = () => {
        setOpenAddItem(true);
        setItemDetails({});
    }

    const onSaveItem = () => {
        let canContinue = true;

        // 1. SENDER DETAILS VALIDATIONS
        Object.keys(itemDetailValidations).forEach((key, index) => {
            if ((!itemDetails[key]) || itemDetails[key] === "") {
                setItemDetails(prevState => ({
                    ...prevState, [key]: "", error: true,
                    errorMessage: { ...prevState.errorMessage, [key]: itemDetailValidations[key] }
                }));
                canContinue = false;
            }
        });

        // 3. Continue if no errors.
        if (canContinue) {
            if (editItemIndex !== null) {
                let itemArray = [...itemList];
                itemArray.splice(editItemIndex, 1);
                setItemList([...itemArray, itemDetails]);
                setEditItemIndex(null);
            } else {
                // SAVE ITEM
                setItemList([...itemList, itemDetails]);
            }

            setOpenAddItem(false);
        } else {
            console.log("Fill mandatory item details.");
        }
    }

    const onCancelAddItem = () => {
        setOpenAddItem(false);
    }

    const onEditItem = (itemIndex) => {
        setItemDetails(itemList[itemIndex]);
        setEditItemIndex(itemIndex);
        setOpenAddItem(true);
    }

    const onHandleNext = () => {
        // TODO: Do validations
        saveAndContinueItem({ itemList });
    }

    return (
        <div>

            <div style={{ display: 'flex', justifyContent: 'flex-end', marginBottom: 15 }}>
                <Button variant="contained" startIcon={<AddIcon />} onClick={onAddNewItem}> Add New Item </Button>
            </div>

            {/* ADDED ITEMS LIST */}
            {
                itemList.length > 0 ?
                    <table className="item-list-table">
                    <thead className="table-header">
                        <tr>
                            <th>S.NO</th>
                            <th>WEIGHT</th>
                            <th>DIMENSION(LxWxH)</th>
                            <th>VOLUME</th>
                            <th>DECLARED VALUE</th>
                            <th>NO.OF ITEMS</th>
                            <th>NOTES</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody className="table-body">
                        {
                            itemList.map((item, index)=> {
                                return <tr>
                                    <td>{index+1}</td>
                                    <td>{item.weight}</td>
                                    <td>{item.length}x{item.width}x{item.height}</td>
                                    <td>{item.volume}</td>
                                    <td>{item.decValue}</td>
                                    <td>{item.itemsCount}</td>
                                    <td>{item.notes}</td>
                                    <td><EditIcon className="edit-item-button" onClick={() => onEditItem(index)}/></td>
                                </tr> 
                            })
                        }
                    </tbody>
                </table>:
                <div className="empty-item">

                    <InfoIcon /> Please add one package item at least.
                </div>
            }

            {/* ADD ITEM POPUP */}
            { openAddItem && 
            <Backdrop sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={true}>
                <Paper style={{ padding: 30 }}>
                <div className="item-details-header">ITEM DETAILS</div>
                <div className="add-item-container">
                    <div className="line-item">
                        <div style={{ display: 'flex', alignItems: 'center' }}>
                            <TextField label="Weight *" name="weight" margin="normal" size="small" className="input-width side-align" onChange={handleInputchange}
                                error={itemDetails?.weight === ""}  value={itemDetails?.weight}/>

                            <TextField label="Length(cm) *" name="length" margin="normal" size="small" className="input-width-120" onChange={handleInputchange}
                                error={itemDetails?.length === ""}  value={itemDetails?.length}/>x

                            <TextField label="Width(cm) *"  name="width" margin="normal" size="small" className="input-width-120" onChange={handleInputchange}
                                error={itemDetails?.width === ""}  value={itemDetails?.width}/>x

                            <TextField label="Height(cm) *" name="height" margin="normal" size="small" className="input-width-120" onChange={handleInputchange}
                                error={itemDetails?.height === ""}  value={itemDetails?.height}/>
                        </div>
                        <div>
                            <TextField label="Volume *" name="volume" margin="normal" size="small" className="input-width side-align" onChange={handleInputchange}
                                error={itemDetails?.volume === ""}  value={itemDetails?.volume}/>

                            <TextField label="Declared Value *" name="decValue" margin="normal" size="small" className="input-width-150 side-align" onChange={handleInputchange}
                                error={itemDetails?.decValue === ""}  value={itemDetails?.decValue}/>

                            <TextField label="No Of Items *" name="itemsCount" margin="normal" size="small" select className="input-width-150 side-align" onChange={handleInputchange}
                                error={itemDetails?.itemsCount === ""}  value={itemDetails?.itemsCount}> 
                            { [1,2,3,4,5].map((option) => (
                                <MenuItem key={option} value={option}>
                                    {option}
                                </MenuItem>
                            ))}
                            </TextField>
                        </div>
                    </div>
                    <div className="line-item">
                        <TextField label="Notes" name="notes" margin="normal" size="small" fullWidth multiline rows={4} className="side-align" onChange={handleInputchange}
                            value={itemDetails?.notes}/>
                    </div>
                </div>  
                {/* SAVE and CANCEL Actions */}
                <div style={{ display: "flex", flexDirection: 'row', justifyContent: 'flex-end', marginBottom: 20, marginTop: 30 }}>
                    <Button variant="outlined" onClick={onCancelAddItem} style={{  marginRight: '30px' }}> Cancel </Button>
                    <Button variant="contained" onClick={onSaveItem}> Save </Button>
                </div>
                </Paper>
            </Backdrop>}

            {/* Handling NEXT */} 
            <div style={{ display: 'flex', justifyContent: 'flex-end', marginTop: '30px' }}>
                <Button variant="contained" endIcon={<NavigateNextIcon />} onClick={onHandleNext} disabled={itemList.length === 0}>
                    Next
                </Button>
            </div>
        </div>
    );
}

export default PackageDetails;